<template>
  <div class="container py-5">
    <Spinner
      :class="{ 'fade-out': !isLoading }"
    />
    <h1 class="mt-5">
      活躍用戶
    </h1>
    <hr />

    <div class="row text-center">

      <div v-for="user in topUsers" :key="user.id" class=" col-md-3 mb-4">

        <div class="card topUser__block shadow-sm ">
          <img class="topUser__cover" loading="lazy" :src="user.cover">
          <div class="card-head topUser__imagine pt-3 pb-3">

            <router-link :to="{
              name: 'profile',
              params: {
                id: user.id
              }
            }
            ">
              <img 
                class="rounded-circle img-thumbnail shadow-sm img__box mxw__user lazy"
                :src="user.avatar"
              >
            </router-link>

          </div>

          <div class="card-body pt-0 pb-2 pr-4 pl-4">
            <h4 class="topUser__name">{{user.name}}</h4>
            <span class="badge badge-pill badge-info ">關注人數：{{user.FollowerCount}}</span>
            <div v-if="currentUser.id !== user.id" class="row justify-content-center mt-3 mb-2">
                  <button 
                    v-if="user.isFollowed"
                    type="submit" 
                    class="btn btn-primary disabled btn-sm"
                    @click.prevent.stop="removeFollow(user.id)"
                  >
                  取消關注
                  </button>

                  <button 
                    v-else
                    type="submit"
                    class="btn btn-primary btn-sm"
                    @click.prevent.stop="addFollow(user.id)"
                  >+ 關注
                  </button>
            </div>

            <div v-else class="row justify-content-center mt-3 mb-2">
              <router-link 
                class="btn btn-success btn-sm"
                :to="{
                  name: 'profile',
                  params: {
                    id: user.id
                  }
                }
              ">
                個人檔案
              </router-link>
            </div>


          </div>

        </div>

      </div>

    </div>

  </div>
</template>

<script>
import userAPI from '../apis/users'
import Spinner from '../components/Spinner'
import { mapState } from 'vuex'
import { iziToast } from '../utils/helpers'

export default {
  name: 'topUsers',
  computed: {
    ...mapState(['currentUser'])
  },
  components: {
    Spinner
  },
  data() {
    return {
      topUsers: [],
      isProcessing: false,
      isLoading: false
    }
  },
  methods: {
    async fetchData() {
      try {
        this.isLoading = true
        const res = await userAPI.getTopUsers()
        const { users } = res.data
        this.topUsers = users
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.$toast.error('', '無法取得用戶資訊', iziToast.options.error)
        this.isLoading = false
      }
    },
    async addFollow(id) {
      try {
        this.isProcessing = true
        const { data } = await userAPI.addFollowing({ id })
        if (data.status !== 'success') {
          this.$toast.error('', data.message, iziToast.options.error)
          throw new Error(data.message)
        }
        this.topUsers = this.topUsers.map(u => {
          if (u.id === id) {
            return {
              ...u,
              isFollowed: true
            }
          }
          return u
        })
        this.$toast.show(' ', data.message, iziToast.options.success)
        this.isProcessing = false
      } catch (err) {
        console.log(err)
        this.$toast.error('', '操作失敗，請稍後再試', iziToast.options.error)
      }
    },
    async removeFollow(id) {
      try {
        this.isProcessing = true
        const { data } = await userAPI.removeFollowing({ id })
        if (data.status !== 'success') {
          this.toastFire('error', data.message)
          throw new Error(data.message)
        }
        this.topUsers = this.topUsers.map(u => {
          if (u.id === id) {
            return {
              ...u,
              isFollowed: false
            }
          }
          return u
        })
        this.$toast.show(' ', data.message, iziToast.options.success)
        this.isProcessing = false
      } catch (err) {
        console.log(err)
        this.$toast.error('', '操作失敗，請稍後再試', iziToast.options.error)
      }
    },

  },
  created() {
    this.fetchData()
  },
}
</script>
